.section-header {
	text-align: center;
	.title {
		margin-bottom: 12px;
	}
	margin-bottom: 35px;
	@include breakpoint(md) {
		.title {
			font-size: 30px;
			margin-bottom: 18px;
		}
		margin-bottom: 45px;
	}
}
.ecommerce-venture-section {
	background: linear-gradient(
		112.54deg,
		#ffffff 0%,
		rgba(var(--base-22), 0.541) 15.13%,
		rgba(255, 255, 255, 0.15) 79.34%,
		#ffffff 99.56%
	);
	padding: 53px 0px 68px;
}
.nav--tabs-wrapper {
	overflow-x: auto;
	padding-bottom: 5px;
}
.nav--tabs {
	border: none;
	gap: 10px;
	justify-content: space-between;
	min-width: 100%;
	width: auto;
	flex-wrap: nowrap;
	li {
		button {
			border: none;
			background: transparent;
			color: var(--base-23);
			width: 102px;
			padding-top: 21px;
			padding-bottom: 21px;
			border-radius: 10px;
			transition: all ease 0.3s;
			white-space: nowrap;
			outline: none;
			box-shadow: none;
			* {
				font-weight: 600;
			}
			&.active {
				animation: fadeIn 0.4s;
				-webkit-animation: fadeIn 0.4s;
				-moz-animation: fadeIn 0.4s;
				color: #ffffff;
				*[stroke="#00A95A"] {
					stroke: #ffffff;
				}
				*[fill="#00A95A"] {
					fill: #ffffff;
				}
				*[stop-color="#00A95A"] {
					stop-color: #ffffff;
				}
				*[stroke="#00C569"] {
					stroke: #ffffff;
				}
				*[fill="#00C569"] {
					fill: #ffffff;
				}
				*[stop-color="#00C569"] {
					stop-color: #ffffff;
				}
				background: linear-gradient(
					119.87deg,
					var(--base-5) 0%,
					var(--base-4) 40.8%,
					var(--base-6) 84.23%
				);
			}
			@include breakpoint(max-md) {
				min-width: 70px;
				padding-top: 14px;
				padding-bottom: 10px;
				padding-right: 10px;
				padding-left: 10px;
				width: unset;
				svg {
					width: 24px;
					height: 24px;
				}
				font-size: 14px;
			}
		}
	}
}
.venture-content {
	&-box {
		.subtitle {
			font-size: 20px;
			font-weight: 600;
		}
		font-weight: 500;
		font-size: 16px;
		margin-bottom: 30px;
		@include breakpoint(max-md) {
			margin-bottom: 20px;
			.subtitle {
				font-size: 18px;
			}
			font-size: 16px;
		}
	}
}

.nav--tabs-wrapper {
	margin-bottom: 40px;
	@include breakpoint(lg) {
		margin-bottom: 44px;
	}
}
.tab-pane.active {
	animation: fadeInUp 0.6s;
	-webkit-animation: fadeInUp 0.6s;
	-moz-animation: fadeInUp 0.6s;
}
.venture-img {
	filter: drop-shadow(0px 4px 50px rgba(0, 0, 0, 0.15));
	img {
		max-width: 100%;
	}
}
.pt-50 {
	padding-top: 50px;
}
.pb-50 {
	padding-bottom: 50px;
}
.pt-30 {
	padding-top: 30px;
}
.pb-30 {
	padding-bottom: 30px;
}
.pt-80 {
	padding-top: 80px;
}
.pb-80 {
	padding-bottom: 80px;
}
.main-category-slider {
	width: calc(100% - 30px);
	max-width: 787px;
	margin: 0 auto;
	.owl-stage-outer {
		overflow: visible;
		.owl-item {
			transition: all ease 0.3s;
			opacity: 0.17;
			&.active {
				opacity: 1;
			}
		}
	}
}
.category-slide-item {
	overflow: hidden;
	border-radius: 10px;
	padding: 25px 61px;
	display: flex;
	align-items: center;
	min-height: 287px;
	position: relative;
	&::after {
		position: absolute;
		content: "";
		inset: 0;
		background: linear-gradient(
			90deg,
			#b0f7d5 0%,
			rgba(196, 196, 196, 0) 91.23%
		);
	}
	div {
		position: relative;
		z-index: 1;
		max-width: 266px;
	}
	.title {
		color: var(--base-1);
		font-weight: 600;
		font-size: 49px;
		line-height: 35px;
		margin-bottom: 10px;
	}
	.text {
		font-weight: 600;
		font-size: 25px;
		line-height: 35px;
		color: #000000;
	}
	@include breakpoint(max-sm) {
		padding: 25px;
		min-height: 267px;
		.title {
			font-size: 42px;
		}
		.text {
			font-size: 20px;
		}
	}
}
.owl-dots {
	display: flex;
	justify-content: center;
	margin-top: 24px;
	gap: 6px;
	.owl-dot {
		width: 10px;
		height: 10px;
		border-radius: 5px;
		background: #e4e4e4;
		transition: all ease 0.3s;
		&.active {
			background: rgba(var(--base-rgb), 0.5);
			width: 20px;
		}
	}
}
.refer-section {
	padding: 36px 0 65px;
}
.learn-feature-section {
	position: relative;
	overflow: hidden;
	&::before {
		content: "";
		inset: 0;
		position: absolute;
		background: rgba(#eefff7, 0.27);
	}
	@include breakpoint(max-lg) {
		.container {
			padding: 0;
		}
	}
}
.learn-feature-content {
	padding: 74px 0 120px;
	@include breakpoint(max-xl) {
		padding-bottom: 0;
		padding-left: 20px;
		padding-right: 20px;
	}
}
.learn-feature-item {
	.learn-feature-icon {
		width: 102px;
		height: 101px;
		background: linear-gradient(
			180deg,
			var(--base-24) 0%,
			var(--base-25) 100%
		);
		box-shadow: 0px 4px 40px rgba(3, 157, 85, 0.15);
		border-radius: 10px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 auto 22px;
		transform: translateY(-34px);
		img {
			max-width: 100%;
		}
		@include breakpoint(max-sm) {
			width: 70px;
			height: 70px;
			svg {
				width: 24px;
			}
		}
	}
	text-align: center;
	.subtitle {
		font-weight: 600;
		font-size: 20px;
		line-height: 24px;
		letter-spacing: 0.05em;
		margin-bottom: 19px;
	}
	font-weight: 300;
	font-size: 16px;
	padding: 0 27px 32px;
	border: 1px solid var(--base-1);
	border-radius: 10px;
	background: var(--white-clr);
	box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.15);
	margin-top: 30px;
	@include breakpoint(max-lg) {
		box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1) !important;
	}
	.learn-feature-item-content {
		margin-top: -34px;
		.text {
			min-height: 52px;
		}
	}
	@include breakpoint(max-sm) {
		font-size: 14px;
		line-height: 1.5;
		padding-left: 15px;
		padding-right: 15px;
	}
}
.learn-feature-item-group {
	div[class*="col"]:nth-child(odd) {
		div[class*="col"] {
			&:nth-of-type(4n + 2) {
				.learn-feature-item {
					.learn-feature-icon {
						background: linear-gradient(
							180deg,
							var(--base-10) 0%,
							var(--base-11) 100%
						);
						box-shadow: 0px 4px 40px rgba(255, 206, 49, 0.25);
					}
				}
			}
			&:nth-of-type(4n + 3) {
				.learn-feature-item {
					.learn-feature-icon {
						background: linear-gradient(
							180deg,
							var(--base-8) 0%,
							var(--base-9) 100%
						);
						box-shadow: 0px 4px 40px rgba(98, 0, 238, 0.25);
					}
				}
			}
			&:nth-of-type(4n + 4) {
				.learn-feature-item {
					.learn-feature-icon {
						background: linear-gradient(
							180deg,
							var(--base-45) 0%,
							var(--base-46) 100%
						);
						background: linear-gradient(
							180deg,
							var(--base-45) 0%,
							var(--base-46) 100%
						);
						box-shadow: 0px 4px 40px rgba(249, 62, 62, 0.25);
					}
				}
			}
		}
	}
	div[class*="col"]:nth-child(even) {
		div[class*="col"] {
			&:nth-of-type(4n + 1) {
				.learn-feature-item {
					.learn-feature-icon {
						background: linear-gradient(
							180deg,
							var(--base-8) 0%,
							var(--base-9) 100%
						);
						box-shadow: 0px 4px 40px rgba(98, 0, 238, 0.25);
					}
				}
			}
			&:nth-of-type(4n + 2) {
				.learn-feature-item {
					.learn-feature-icon {
						background: linear-gradient(
							180deg,
							var(--base-24) 0%,
							var(--base-25) 100%
						);
						box-shadow: 0px 4px 40px rgba(3, 157, 85, 0.15);
					}
				}
			}
			&:nth-of-type(4n + 3) {
				.learn-feature-item {
					.learn-feature-icon {
						background: linear-gradient(
							180deg,
							var(--base-10) 0%,
							var(--base-11) 100%
						);
						box-shadow: 0px 4px 40px rgba(255, 206, 49, 0.25);
					}
				}
			}
			&:nth-of-type(4n + 4) {
				.learn-feature-item {
					.learn-feature-icon {
						background: linear-gradient(
							180deg,
							var(--base-43) 0%,
							var(--base-44) 100%
						);
						box-shadow: 0px 4px 40px rgba(23, 161, 250, 0.25);
					}
				}
			}
		}
	}
	animation: scrollToTop 80s linear infinite;
}
.learn-feature-wrapper {
	height: 430px;
	overflow: visible;
	&::-webkit-scrollbar {
		display: none;
	}
	@include breakpoint(max-lg) {
		overflow: hidden;
		margin-left: 20px;
		margin-right: 20px;
	}
}
@keyframes scrollToTop {
	0% {
		@include transform(translateY(0));
	}
	100% {
		@include transform(translateY(calc(-100% + 422px)));
	}
}
.earn-money-section {
	padding: 62px 0 165px;
	position: relative;
	background: linear-gradient(
		112.54deg,
		#99f5ca51 0%,
		#ffffff 33.19%,
		#ffffff 66.37%,
		#1cff9518 99.56%
	);
	border-bottom-right-radius: 150px;
	overflow: hidden;
	.container-fluid {
		position: relative;
		z-index: 3;
	}
	@include breakpoint(max-lg) {
		padding-bottom: 80px;
	}
}
.earn-item {
	display: flex;
	align-items: center;
	&-img {
		width: 950px;
		flex-grow: 1;
		min-height: 264px;
		padding: 70px;
		border-radius: 0 50px 50px 0;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		position: relative;
		overflow: hidden;
		&::before {
			position: absolute;
			content: "";
			inset: 0;
			background: linear-gradient(
				270deg,
				#195036 13.66%,
				rgba(2, 211, 113, 0) 89.75%
			);
			filter: drop-shadow(0px 4px 40px rgba(174, 255, 217, 0.25));
		}
		@include breakpoint(max-lg) {
			min-height: 230px;
		}
		@media (min-width: 1850px) {
			min-height: 280px;
		}
		@include breakpoint(max-md) {
			padding: 50px 30px;
		}
		@include breakpoint(max-sm) {
			max-width: calc(100% - 40px);
		}
		@media (max-width: 375px) {
			max-width: calc(100% - 20px);
			.cmn--btn {
				font-size: 14px;
			}
			min-height: 200px;
		}
	}
	&-cont {
		padding: 44px;
		width: 485px;
		flex-grow: 1;
		display: flex;
		.subtitle {
			font-size: 20px;
		}
		.title {
			font-weight: 800;
			font-size: 28px;
			color: var(--base-1);
			margin-top: 5px;
		}

		@include breakpoint(max-md) {
			padding: 30px;
		}
	}

	@include breakpoint(max-sm) {
		flex-wrap: wrap-reverse;
	}
	&:not(:last-child) {
		margin-bottom: 58px;
	}
	&:nth-child(even) {
		flex-direction: row-reverse;
		.earn-item-img {
			border-radius: 50px 0 0 50px;
			justify-content: flex-start;
			&::before {
				background: linear-gradient(
					-270deg,
					#195036 13.66%,
					rgba(2, 211, 113, 0) 89.75%
				);
			}
		}
		.earn-item-cont {
			justify-content: flex-end;
			@include breakpoint(xl) {
				> div {
					padding-right: 26px;
				}
			}
		}
	}
}
/*Earn Money Shapes*/
