/*Counter Section*/
.counter-wrapper {
	padding: 40px 0 95px 25px;
	.counter-bg {
		border: 1px solid #c6fde3;
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 100%;
		background: #ffffff;
		@include breakpoint(lg) {
			mask: url(./img/counter-shape.png) no-repeat;
			-webkit-mask: url(./img/counter-shape.png) no-repeat;
			mask-size: 100% 100%;
			-webkit-mask-size: 100% 100%;
		}
		border-radius: 45px;
	}
	filter: drop-shadow(0px 4px 40px rgba(0, 138, 73, 0.15));
	-webkit-filter: drop-shadow(0px 4px 40px rgba(0, 138, 73, 0.15));
	.contact-wrapper-inner {
		position: relative;
		z-index: 1;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		.left-side {
			flex-grow: 1;
			width: 420px;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-evenly;
			gap: 25px;
		}
		.right-side {
			padding: 27px 28px;
			background: #f1fff9;
			border: 1px solid #8ef1c3;
			box-shadow: 0px 4px 40px rgba(52, 206, 134, 0.12);
			border-radius: 27px;
			font-weight: 700;
