@keyframes shake-1 {
	0% {
		transform: rotate(0);
	}
	10% {
		transform: rotate(0);
	}
	40% {
		transform: rotate(-15deg);
	}
	60% {
		transform: rotate(-15deg);
	}
	90% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(0);
	}
}
@keyframes shake-2 {
	0% {
		transform: rotate(0);
	}
	10% {
		transform: rotate(0);
	}
	40% {
		transform: rotate(12deg);
	}
	60% {
		transform: rotate(12deg);
	}
	90% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(0);
	}
}
@keyframes shake-3 {
	0% {
		transform: rotate(0);
	}
	10% {
		transform: rotate(0);
	}
	40% {
		transform: rotate(-8deg);
	}
	60% {
		transform: rotate(-8deg);
	}
	90% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(0);
	}
}

